
import { AfterViewInit, Component, Input, OnInit } from '@angular/core';
import { BcDataTableBehavior, BcDataTableConfig } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';


/**
 * Importa las alertas con los dise√±os de Bancolombia S.A.
 * El componente data-table representa un indicador de novedad sobre un elemento
 *
 * @export
 * @class BcDataTableComponent
 * @implements {AfterViewInit}
 */
 @Component({
    selector: 'bc-data-table',
    templateUrl: 'bc-data-table.component.html',
    styles: [],
  })

  
  export class BcDataTableComponent implements OnInit, AfterViewInit {
    bcDataTableBehavior: BcDataTableBehavior;
    dataTableId;
    /**
     * Configuracion de datatable.
     *
     * @memberof BcDataTableConfig
     */

    constructor( private bcHelperService: BcHelperService ) { }

    @Input('data-table-config') dataTableConfig: BcDataTableConfig;
    
    ngOnInit(): void {
      this.dataTableId = this.bcHelperService.getId(this);
      this.dataTableConfig.id = this.dataTableId;      
    }

    ngAfterViewInit(): void {
      this.bcDataTableBehavior = new BcDataTableBehavior(this.dataTableConfig);
    }

    updateData(dataTableConfig:BcDataTableConfig):void{
      this.bcDataTableBehavior = new BcDataTableBehavior(dataTableConfig);
    }
}
  
